export default {
  data() {
    return {
      tabClass: null,
    }
  },
  methods: {
    callback(key) {
      this.$router.push({ name: key })
    },
    tabsAffix(affixed) {
      if (affixed) {
        this.tabClass = 'index-affixed'
      } else {
        this.tabClass = null
      }
    },
  },
}
